.App{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form-group{
  margin-top: 10px;
  display: flex;
  flex-direction: column;
}
.total{
  display: flex;
  align-items:flex-end;
}
input{
  border-radius: 5px;
  margin-bottom: 10px;
}
label{
  padding-bottom: 7px;
  font-weight: 600;
}
.btn{
  background-color: grey;
  color: white;
  font-weight: 800;
  margin-bottom: 20px;
}
.button-action{

  margin-left: 10px;
 
}
.button-action .btn{
  margin-bottom: 6px;
  background-color: red;
  font-weight: 600;
}
.alert{
  width: 78%;
  margin: auto;
  padding: 5px;
}